import React, { Fragment } from 'react';
import { Tag } from 'baseui/tag';

const ShowChats = ({ chats }) => {
  return (
    <Fragment>
      {chats && chats.length
        ? chats.map((chat, index) => (
            <div key={index}>
              <Tag>{chat.message}</Tag>
            </div>
          ))
        : null}
    </Fragment>
  );
};

export default ShowChats;
